body {
    background-color: black;
    color: white;
    font-family: Arial, Helvetica, sans-serif;
}

.container {
    margin: 2%;
}

.left-column {
    float: left;
    width: 70%;
}

.right-column {
    float: left;
    width: 30%;
}

.right-column img {
    float: right;
}

td {
    padding: 15px;
    text-align: center;
    text-decoration: none;
}

td caption {
    padding-top: 15px;
    text-transform: uppercase;
}

a {
    text-decoration: none;
    color: inherit;
}

td img {
    border-radius: 25px;
}